.reminder-wrap {
	position: relative;
	display: block;
	.orn-1 {
		position: relative;
		width: 50%;
		margin: 1rem 0 2rem;
		@include max-md {
			width: 70%;
		}
		@include max-sm {
			width: 100%;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(10%, 20%) rotate(15deg);
			@include min-lg {
				width: 220px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(-10%, 0) scaleX(-1);
			@include min-lg {
				width: 240px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(20%, 0) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 320px;
		}
		@include min-xl {
			width: 35%;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		z-index: 2;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
