.couple-section {
	position: relative;
	text-align: center;
	padding: 5rem 0;
	color: $color-custom;
	@include max-md {
		padding: 8rem 0 3rem;
	}
	.and {
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		.orn-1 {
			position: absolute;
			top: -9rem;
			left: 0;
			width: 180px;
			transform: rotate(-18deg);
			@include min-md {
				top: -5rem;
				width: 240px;
				transform: translate(-30%, -20%) rotate(-18deg);
			}
			@include min-lg {
				width: 260px;
			}
		}
		.orn-2 {
			position: absolute;
			top: -8rem;
			right: 0;
			width: 180px;
			transform: translate(20%, 0) rotate(12deg) scaleX(-1);
			@include min-md {
				top: -5rem;
				width: 240px;
				transform: translate(20%, -10%) rotate(12deg) scaleX(-1);
			}
			@include min-lg {
				width: 260px;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: translate(10%, 0) scale(-1) rotate(-70deg);
			z-index: 2;
			@include min-lg {
				width: 200px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 30%;
			right: 0;
			width: 160px;
			transform: translate(20%, 0) scaleX(-1) rotate(-10deg);
			z-index: 2;
			@include min-md {
				bottom: 0;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-10%, 0) scaleY(-1) rotate(-75deg);
			z-index: 2;
			@include min-lg {
				width: 200px;
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 30%;
			left: 0;
			width: 160px;
			transform: translate(-20%, 0) rotate(-10deg);
			z-index: 2;
			@include min-md {
				bottom: 0;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 100px;
			transform: translate(-70%, 0);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		&.man {
			text-align: left;
			.sosmed-wrap {
				margin: 0 auto 0 0;
			}
		}
		&.woman {
			text-align: right;
			.sosmed-wrap {
				margin: 0 0 0 auto;
			}
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 3/4;
			margin: 0 auto;
			@include max-md {
				&.man {
					width: 80%;
					margin: 0 auto 0 0;
				}
				&.woman {
					width: 80%;
					margin: 0 0 0 auto;
				}
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 8px;
					box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
			color: $color-custom;
			@include max-md {
				padding: 2rem 0 0;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				background: var(--color-secondary);
				padding: 0.5rem 0.6rem;
				border-radius: 50px;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
