.wishes-section {
	position: relative;
	.wishes-form {
		position: relative;
		padding: 5rem 0 1rem;
		@include max-md {
			padding: 3rem 0 1rem;
		}
		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200px;
			transform: translate(-50%, 10%);
			@include min-md {
				width: 260px;
				transform: translate(-40%, 10%);
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 200px;
			transform: translate(50%, -30%) scaleX(-1) rotate(-5deg);
			@include min-md {
				width: 260px;
				transform: translate(35%, -20%) scaleX(-1) rotate(-5deg);
			}
			@include min-lg {
				width: 320px;
			}
		}
	}
	.card-form {
		margin-bottom: 0;
		background: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 1rem 0 5rem;
		@include max-md {
			padding: 1rem 0 3rem;
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.6);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.6);
							}
						}
					}
				}
			}
		}
	}
}
