.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: relative;
		width: 50%;
		margin-top: -5rem;
		@include max-md {
			margin-top: -3rem;
			width: 80%;
		}
		@include max-sm {
			width: 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 0) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 320px;
		}
		@include min-xl {
			width: 35%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 160px;
		transform: translate(-80%, -20%) rotate(25deg);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 220px;
		}
		@include min-xl {
			transform: translate(-80%, -45%) rotate(25deg);
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
