.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 50%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 50%;
		transform: translate(0, -20%) scaleX(-1);
		z-index: 1;
		@include min-md {
			width: 40%;
		}
		@include min-lg {
			width: 30%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(20%, -10%) scaleX(-1);
		z-index: 1;
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-30%, -30%);
		z-index: 1;
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, 0) rotate(10deg) scaleX(-1);
		z-index: 1;
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(-30%, 0) rotate(-10deg);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(-30%, 10%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.header-content {
		position: relative;
		min-height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 4rem 0 3rem;
		.header {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			min-width: 70%;
			max-width: 100%;
			padding: 3rem 2rem;
			min-height: 60vh;
			box-shadow: inset 0 0 2rem rgba($color: #000000, $alpha: 0.15);
			@include max-lg {
				min-width: 80%;
			}
			@include max-md {
				min-width: 100%;
				min-height: 80vh;
			}
			.header-title {
				position: relative;
				text-align: center;
				z-index: 3;
				&-content {
					position: relative;
					color: $color-custom;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
