.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, -50%) scaleX(-1) rotate(-10deg);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 320px;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 100px;
			transform: translate(-70%, 5%);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-45%, 20%);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 320px;
		}
		@include min-xl {
			width: 340px;
		}
	}
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		.title-section {
			text-align: left;
			margin: 0 0 1.25rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
