.stream-section {
	position: relative;
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			width: 100%;
			height: 100%;
			.stream-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		padding: 5rem 0;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		@include max-md {
			padding: 3rem 0;
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				background: var(--color-light);
				border-radius: var(--border-radius);
				@include max-md {
					padding: 2rem 1rem;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
	}
}
