.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-40%, -40%) rotate(15deg);
		@include min-md {
			width: 260px;
			transform: translate(-30%, -40%) rotate(15deg);
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, -40%) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 320px;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 100px;
			transform: translate(-20%, 20%) rotate(-10deg);
			@include min-md {
				width: 140px;
				transform: translate(-40%, 0) rotate(-10deg);
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 16/9;
		margin: 0 auto;
		@include max-md {
			aspect-ratio: 3/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			&::after {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				border-radius: 8px;
				box-shadow: inset 0 0 1rem rgba($color: #000000, $alpha: 0.15);
			}
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 8px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
